import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import { CustomButton, CustomDateRangePicker } from '../../globalComponents';
import { employeeActions } from '../../../core/resource/actions';
import { toast } from 'react-toastify';
import { pagination } from '../../../utils/Constants.config'
import { MultiSelect } from "react-multi-select-component";
import moment from 'moment'
import { shiftManagementAction } from '../../../core/resource/actions';
import { convertToTS } from '../../../utils/AppHelper';



const AssignShift = (props) => {
    const dispatch = useDispatch()
    const { openAssignShiftModel, assignShiftModelOpen, shiftData,getAllShiftDetails,setOpenEditModel } = props
    const {
        getAllEmployeePending,
        getAllEmployeeRes,
        getAllEmployeeError,
    } = useSelector(state => state.employee)

    const {
        assignShiftPending,
        assignShiftRes,
        assignShiftError,
    } = useSelector(state => state.shift)

    const [currentPage, setCurrentPage] = useState(1);
    const [errors, setErrors] = useState({});
    const [getAllEmployee, setGetAllEmployee] = useState([])
    const [selectEmployee, setSelectEmployee] = useState([])
    const [startDate, setStartDate] = useState('')
    const [endDate, setEndDate] = useState('')

    const [assignShift, setAssignShift] = useState({
        assignment_type:'',
        shift_id:'',
        employee_id:'',
        start_date:'',
        end_date:''
    })

    // get All Employees
    useEffect(() => {
        getAllEmployees()
    }, [])

    useEffect(() => {
        if (!assignShiftPending) {
            if (assignShiftError) {
                toast.error(assignShiftError?.errorMessage)
                return;
            }
            if (assignShiftRes) {
                if (assignShiftRes?.code === 200) {
                    toast.success(assignShiftRes?.data?.msg)
                    assignShiftModelOpen()
                    clearFunction()
                    getAllShiftDetails(shiftData)
                    setOpenEditModel(false)
                    dispatch(shiftManagementAction.handleClearShiftStore("HANDLE_CLEAR_ASSIGN_SHIFT_STORE"))
                }
            }
        }
    }, [
        assignShiftRes,
        assignShiftError,
        assignShiftPending
    ]);

    const getAllEmployees = () => {
        const params = {
            page: currentPage,
            limit: pagination.limit,
        }
        dispatch(employeeActions.getAllEmployeeRequest(params))
    }

    useEffect(() => {
        if (!getAllEmployeePending) {
            if (getAllEmployeeError) {
                return;
            }
            if (getAllEmployeeRes) {
                if (getAllEmployeeRes == null) {
                    getAllEmployees()
                }
                if (getAllEmployeeRes?.code === 200) {
                    const getAllEmployeeResData = getAllEmployeeRes?.data
                    setGetAllEmployee(getAllEmployeeResData)

                }
            }
        }
    }, [
        getAllEmployeeRes,
        getAllEmployeeError,
        getAllEmployeePending
    ]);

    const handleChange = (event) => {
        const { name, value } = event.target;
        setAssignShift({ ...assignShift, [name]: value });
        setErrors(prev => ({ ...prev, [name]: undefined }));
    };
    
    const handleSubmitAssignShift = () => {
        assignShift.shift_id = shiftData?.id
        assignShift.employee_id = selectEmployee?.map(data=>data?.value)
        // const validationResponse = await validateSchema(addShiftSchema, shiftConfigurationdata);
        // if (validationResponse.isValid) {
        setErrors({});
        dispatch(shiftManagementAction.assignShiftRequest(assignShift));
        
    }
    const clearFunction = ()=>{
        setAssignShift({
            assignment_type:'',
            shift_id:'',
            employee_id:'',
            start_date:'',
            end_date:''
        })
        setSelectEmployee([])
    }
    return (
        <>
            <div className={`offcanvas offcanvas-end applyLeaveCanvas ${openAssignShiftModel ? 'show' : ''}`}

                tabIndex={-1}
                style={{
                    visibility: openAssignShiftModel ? 'visible' : 'hidden',
                    width: "700px"
                }}>
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">Assign Shift</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => assignShiftModelOpen()}
                    />
                </div>
                <div className="offcanvas-body">
                    <div className="employeeAdd rounded-0 border-0">
                        <div className="employeeAddheader justify-content-between align-items-center border-0 m-0 mb-3">
                            <div className="row">
                                <div className="col-md-3">
                                    <div className="form-group mb-3">
                                        <label htmlFor><b>Shift Name</b></label>
                                    </div>
                                </div>
                                <div className="col-md-9">
                                    <div className="form-group mb-3">

                                        {shiftData.shift_name} ({shiftData.shift_start_time} - {shiftData.shift_end_time})
                                    </div>
                                </div>
                            </div>

                        </div>


                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor>Shift Assignment Type<span className='text-danger'>*</span></label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <select className="form-select" required
                                        name='assignment_type' onChange={(e)=>handleChange(e)}
                                    >
                                        <option value='' hidden selected>Select Shift Assignment Type</option>
                                        <option value='default'>Default</option>
                                        <option value='monthly'>Monthly</option>
                                        <option value='temporary'>Temporary</option>
                                    </select>
                                </div>
                            </div>
                        </div>
                        {assignShift?.assignment_type === 'monthly' &&
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor style={{ position: "relative", top: "30px" }}>Shift Range<span className='text-danger'>*</span></label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <div className='row'>
                                        <div className="form-group col-md">
                                            <label htmlFor="exampleInputEmail1">Select Month</label><span className='text-danger'>*</span>
                                            <input type="month" name="start_date" onChange={(e)=>handleChange(e)} className="form-control" placeholder="Enter start time" />
                                        </div>
                                        {/* <div className="form-group col-md">
                                            <label htmlFor="exampleInputEmail1">End Date</label><span className='text-danger'>*</span>
                                            <input type="date" name="end_date" onChange={(e)=>handleChange(e)} min={moment(assignShift?.start_date).format('YYYY-MM-DD')} className="form-control" placeholder="Enter end time" />
                                        </div> */}
                                    </div>
                                </div>
                            </div>
                        </div>}
                        {assignShift?.assignment_type !== 'default' && assignShift?.assignment_type !== 'monthly' &&
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor style={{ position: "relative", top: "30px" }}>Shift Range<span className='text-danger'>*</span></label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <div className='row'>
                                        <div className="form-group col-md">
                                            <label htmlFor="exampleInputEmail1">Start Date</label><span className='text-danger'>*</span>
                                            <input type="date" name="start_date" onChange={(e)=>handleChange(e)} className="form-control" placeholder="Enter start time" />
                                        </div>
                                        <div className="form-group col-md">
                                            <label htmlFor="exampleInputEmail1">End Date</label><span className='text-danger'>*</span>
                                            <input type="date" name="end_date" onChange={(e)=>handleChange(e)} min={moment(assignShift?.start_date).format('YYYY-MM-DD')} className="form-control" placeholder="Enter end time" />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>}

                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor>Select Employee<span className='text-danger'>*</span></label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">

                                <MultiSelect
                                        options={getAllEmployee.map(usr => {
                                            usr.label = usr.label
                                            usr.value = usr.value
                                            usr.emp_user_id = usr.id
                                            return usr
                                        })}
                                        value={selectEmployee}
                                        onChange={setSelectEmployee}
                                        labelledBy="Select Employee"
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <button type="button" className="btn btn-primary gradientBg" onClick={handleSubmitAssignShift}>Assign</button>
                    </div>
                </div>
            </div>
        </>
    )
}

export default AssignShift;
