import React from "react";
import moment from 'moment'

const ShiftTableHeader = (props) => {
  const {headerDate} = props
  return (
    <thead>
      <tr>
        <th className="searchTh">
          <div className="searchByName">
            <input
              type="text"
              className="form-control"
              placeholder="Search Employee by Name"
            />
            <button type="button" className="nameSearchBtn">
              <img src="images/search-box.svg" alt="" width={14} />
            </button>
          </div>
        </th>
        {headerDate && headerDate?.map(item=>(
           <th>
            {moment(item.date).format('ddd DD')}
         </th>
        ))}
      </tr>
    </thead>
  );
};

export default ShiftTableHeader;
