import React, { useState } from "react";
import ShiftTableRow from "./ShiftTableRow";
import { useSelector } from "react-redux";
import EmployeeInfoCard from "./EmployeeInfoCard";
import EmployeeShiftNameTime from "./EmployeeShiftNameTime";

const ShiftTableBody = (props) => {
  const {allEmployeeShift,shiftData,editMode,setOpenEditModel,openEditModel,setAssignTemporaryShift} = props

  // const { weekFirstDate, weekEndDate,shiftId } = useSelector(
  //   (state) => state.shiftCalanderHeader
  // );
 
  const data = null;
  const isLoading = false;
  const isSuccess = false;
  const isError = false;
  

  return (
    <tbody>
      {/* <ShiftTableRow /> */}
      {isLoading ? (
        <tr>
          <td>Loading....</td>
        </tr>
      ) : isError ? (
        <tr>
          <td>Something went wrong....</td>
        </tr>
      ) : (
        allEmployeeShift && allEmployeeShift?.slice(0,10)?.map((userShiftData) => (
          <tr key={userShiftData?.id} onClick={()=>setAssignTemporaryShift(userShiftData)}>
            <EmployeeInfoCard userShiftData={userShiftData} 
            shiftData={shiftData}
            editMode={editMode}/>
            {userShiftData?.shiftDetails.map((shiftTime) => (
              <EmployeeShiftNameTime
                key={shiftTime?.date}
                shiftTime={shiftTime}
                editMode={editMode}
                openEditModel={openEditModel}
                setOpenEditModel={setOpenEditModel}
              />
            ))}
          </tr>
        ))
      )}
    </tbody>
  );
};

export default ShiftTableBody;
