import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import moment from 'moment-timezone'
import { useDispatch, useSelector } from "react-redux";
import { shiftManagementAction } from '../../../core/resource/actions';
import ApproveRejectModal from '../../modal/ApproveRejectModal'

const EmployeeInfoCard = ({userShiftData,shiftData,editMode}) => {
  const dispatch = useDispatch()
  const firstDateOfMonth=new Date(new Date().getFullYear(),new Date().getMonth(),1)
  const lastDateOfMonth=new Date(new Date().getFullYear(),new Date().getMonth()+1,0)
  const [isShowDeleteModal,setIsShowDelelteModal] = useState(false)
  const [employeeId, setEmployeeId] = useState('')

  const {
    deleteEmployeeShiftPending,
    deleteEmployeeShiftRes,
    deleteEmployeeShiftError,
} = useSelector(state => state.shift)

  const onHandleDeleteModal = (id) => {
    setIsShowDelelteModal(!isShowDeleteModal)
    setEmployeeId(id)
  }

  const onDeleteEmployee = (type) => {
    if (type) {
      const params = {
        employee_id: employeeId
      }
      dispatch(shiftManagementAction?.deleteEmployeeShiftRequest(params))
      setIsShowDelelteModal(!isShowDeleteModal)
    } else {
      setIsShowDelelteModal(!isShowDeleteModal)
    }
  }
  return (
    <>
      <td>
      <div className="d-flex justify-content-between align-items-center">
      <Link to={`/shift-management/${userShiftData?._id}/${moment(firstDateOfMonth).format("YYYY-MM-DD")}/${moment(lastDateOfMonth).format("YYYY-MM-DD")}`}>
        <a href="shift-view.html" className="userName">
          <figure>
            <img src="images/profile-img.png" alt="" /> 
          </figure>
          <p>
            <strong>{userShiftData?.first_name}</strong>
            {userShiftData.emp_id} | {userShiftData.Designation}
          </p>
        </a>
        </Link>
        <div className="deleteBG">
          {shiftData && shiftData.shift_name !== 'All Shift' && editMode &&
          <img src="images/delete-icon.svg" alt="" width={16} height={20} onClick={()=>onHandleDeleteModal(userShiftData?.employee_id)}/>}
        </div>
      </div>
      
    </td>
    {isShowDeleteModal && (
      <ApproveRejectModal
        onApproveReject={onDeleteEmployee}
        isShow={isShowDeleteModal}
        desc={"You want to delete this Employee"}
        isLoading={deleteEmployeeShiftPending}
      />)}
      </>
  )
}

export default EmployeeInfoCard