import React, { useState,useEffect } from "react";
import ShiftCalanderHeader from "./ShiftCalanderHeader";
import ShiftTableHeader from "./ShiftTableHeader";
import ShiftTableBody from "./ShiftTableBody";
import { useDispatch, useSelector } from "react-redux";

const ShiftCalender = (props) => {
  const {shiftData,isActiveShift,editMode,setOpenEditModel,openEditModel,setAssignTemporaryShift,setLoadIng} = props
  const {
    getAllShiftDetailsPending,
    getAllShiftDetailsRes,
    getAllShiftDetailsError,
} = useSelector(state => state.shift)
  const [headerDate, setHeaderDate] = useState([])
  const [allEmployeeShift, setAllEmployeeShift] = useState([])

  useEffect(() => {
    if (!getAllShiftDetailsPending) {
        if (getAllShiftDetailsError) {
            return;
        }
        if (getAllShiftDetailsRes) {
            if (getAllShiftDetailsRes?.code === 200) {
                setLoadIng(false)
                const EmployeeResData = getAllShiftDetailsRes?.data
                setHeaderDate(EmployeeResData?.shiftDateRange)
                setAllEmployeeShift(EmployeeResData?.getEmployeeDetails)
                
            }
        }
    }
}, [
  getAllShiftDetailsRes,
  getAllShiftDetailsError,
  getAllShiftDetailsPending
]);
  
  return (
    <div className="cardBox p-0 mb-3 customCalander overflow-hidden">
      <div className="tab-content">
        <div className="tab-pane fade show active" id="shiftTab1">
          <ShiftCalanderHeader
          setLoadIng={setLoadIng}
          shiftData={shiftData}
          headerDate={headerDate}
          allEmployeeShift={allEmployeeShift}
          isActiveShift={isActiveShift}
          setOpenEditModel={setOpenEditModel}/>
          <div className="table-responsive shiftTablemain shiftHeigth">
            <table className="table m-0">
              <ShiftTableHeader 
              headerDate={headerDate}/>
              <ShiftTableBody 
              allEmployeeShift={allEmployeeShift}
              shiftData={shiftData}
              editMode={editMode}
              openEditModel={openEditModel}
              setOpenEditModel={setOpenEditModel}
              setAssignTemporaryShift={setAssignTemporaryShift}/>
            </table>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ShiftCalender;
