import React from 'react'
import CreateUpdateShift from '../../modal/shiftManagement/CreateUpdateShift'

const ShiftMenuDropdown = (props) => {
  const { card, setEditShift, openCreateShiftModel, setDeletShift, setActivebtn } = props
  const editFunction = () => {
    openCreateShiftModel()
    setEditShift(card)
  }
  return (
    <>
      <div className="dropdown">
        <button
          className="btn dropdownDots"
          type="button"
          data-bs-toggle="dropdown"
          aria-expanded="false"
        >
          <img src="images/dotvertical.svg" alt="" width={25} />
        </button>
        <ul className="dropdown-menu">
          <li>
            <button
              className="dropdown-item border-bottom"
              onClick={() => editFunction()}
            >
              Edit Shift
            </button>
          </li>
          {card.is_default != true &&
            <li>
              {card.is_active == true ?
                <button className="dropdown-item border-bottom" onClick={() => setActivebtn(card)}>
                  Set as Inactive
                </button> :
                <button className="dropdown-item border-bottom" onClick={() => setActivebtn(card)}>
                  Set as active
                </button>}

            </li>}
          <li>
            <button className="dropdown-item" onClick={() => setDeletShift(card)}>
              Delete Shift
            </button>
          </li>
        </ul>
      </div>

    </>
  )
}

export default ShiftMenuDropdown