import React, { useState } from "react";

import ShiftSchedualCards from "./ShiftSchedualCards";
import CreateUpdateShift from '../../modal/shiftManagement/CreateUpdateShift'
import AddCalendar from "../../modal/shiftManagement/AddCalendar";
import { useNavigate } from 'react-router-dom';

const ShiftSchedual = (props) => {
  const navigate = useNavigate();

  const {getAllShift,getAllShiftFunction,setDeletShift,setShiftData,setActivebtn,editMode,setEditMode,openUploadShiftModel,setOpenUploadShiftModel} = props

  const [openModel,setOpenModel] = useState(false)
  const [openAddCalendar, setAddCalendar] = useState(false)
  const [editShift, setEditShift] = useState('')
  

  const openCreateShiftModel = ()=>{
    setOpenModel(!openModel)

  }
  const openAddCalendarModel = ()=>{
    navigate('/shift-setting');
    // setAddCalendar(!openAddCalendar)

  }
  return (
    <div className="shiftSchedule mb-3">
      <div className="cardHead d-flex justify-content-between gap-3">
        <h3 className="mb-0">Shift Schedule (Weekly)</h3>
        <div>
          {editMode &&
          <>
          <button className="btn btn-primary me-2" 
          onClick={()=>setOpenUploadShiftModel(!openUploadShiftModel)}>
            Shift Bulk Upload
          </button>
          <button className="btn btn-primary me-2"
            // disabled={(User[0].panel_role == "manager_shift") ? true : false}
            onClick={()=>openCreateShiftModel()}
            >
            Create Shift
          </button>
          
          <button className="btn btn-primary me-2"
          onClick={()=>openAddCalendarModel()}
            // disabled={((userByshift.length > 0 && userByshift[0]?.shift_configuration === 0) && User[0].panel_role == "manager_shift") ? true : false}
            // onClick={() => setshiftEdit("2")}
            >
            Setting
          </button>
          </>}
          <button className="btn btn-primary "
            // disabled={((userByshift.length > 0 && userByshift[0]?.shift_configuration === 0) && User[0].panel_role == "manager_shift") ? true : false}
            onClick={() => setEditMode(!editMode)}
            >
            Edit
          </button>
        </div>
      </div>
      <CreateUpdateShift 
      openCreateShiftModel={openCreateShiftModel}
      openModel={openModel}
      editShift={editShift}
      setEditShift={setEditShift}
      getAllShiftFunction={getAllShiftFunction}
      />
      <ShiftSchedualCards 
      getAllShift={getAllShift}
      setEditShift={setEditShift}
      openCreateShiftModel={openCreateShiftModel}
      setDeletShift={setDeletShift}
      setActivebtn={setActivebtn}
      setShiftData={setShiftData}
      editMode={editMode}/>
      <AddCalendar 
      openAddCalendar={openAddCalendar}
      openAddCalendarModel={openAddCalendarModel}
      />
    </div>
  );
};

export default ShiftSchedual;
