import React, { useEffect, useState } from "react";
import ShiftInfoCard from "./ShiftInfoCard";

const ShiftSchedualCards = (props) => {
  const {getAllShift,setEditShift,openCreateShiftModel,setDeletShift,setShiftData,setActivebtn,editMode} = props

  // console.log("Hello selected data", setShiftData)

  const data = null;
  const isLoading = true;
  const isSuccess = false;
  const [box, setBox] = useState(null);

  const scrollRight = () => {
    let width = box.clientWidth;
    box.scrollLeft = box.scrollLeft + width;
  };
  const scrollLeft = () => {
    let width = box.clientWidth;
    box.scrollLeft = box.scrollLeft - width;
  };
  useEffect(() => {
    let box = document.querySelector(".qualityServicetab");
    setBox(box);
  }, [box]);
  return (
    <div className="carousel slide " style={{ overflow: "hidden" }}>
      <ul
        className="nav nav-tabs shiftListing qualityServicetab border-0"
        style={{ overflowX: "hidden", scrollBehavior: "smooth", marginRight:"70px",marginLeft:'70px' }}
      >
        
        {getAllShift&&getAllShift?.map((card,index)=>(
         <ShiftInfoCard card={card} 
         key={card?.id} 
         index={index} 
         setEditShift={setEditShift} 
         openCreateShiftModel={openCreateShiftModel} 
         setDeletShift={setDeletShift} 
         setShiftData={setShiftData} 
         setActivebtn={setActivebtn}
         editMode={editMode}/>

        ))}
        
      
      </ul>
      <button
        className="carousel-control-prev scrollbtn"
        type="button"
        data-bs-target="#carouselExampleCaptions"
        data-bs-slide="prev"
        onClick={scrollLeft}
      >
        <span
          className="carousel-control-prev-icon "
          style={{ position: "absolute", left: "0px" }}
          aria-hidden="true"

        ></span>
        <span className="visually-hidden">Previous</span>
      </button>
      <button
        className="carousel-control-next scrollbtn"
        type="button"
        data-bs-target="#carouselExampleCaptions"
        data-bs-slide="next"
        onClick={scrollRight}
      >
        <span
          style={{ position: "absolute", right: "0px" }}
          className="carousel-control-next-icon"
          aria-hidden="true"
        ></span>
        <span className="visually-hidden">Next</span>
      </button>
    </div>
  );
};

export default ShiftSchedualCards;
