import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { months } from "../../../utils/Constants.config";
import ShiftConfiguration from '../../modal/shiftManagement/ShiftConfiguration'
import AssignShift from '../../modal/shiftManagement/AssignShift'
import { shiftManagementAction } from '../../../core/resource/actions'
import AddWeekOff from "../../modal/shiftManagement/AddWeekOff";
import moment from 'moment'

const ShiftCalanderHeader = (props) => {
  const { shiftData, headerDate, allEmployeeShift,isActiveShift,setOpenEditModel, setLoadIng } = props
  // const{date,shiftCalanderMonth,shiftCalanderYear,weekFirstDate,weekEndDate}=useSelector((state)=>state?.shiftCalanderHeader)

  const {
    deleteEmployeeShiftPending,
    deleteEmployeeShiftRes,
    deleteEmployeeShiftError,
  } = useSelector(state => state.shift)

  const dispatch = useDispatch()
  const [openShiftModel, setOpenShiftModel] = useState(false)
  const [openAssignShiftModel, setOpenAssignShiftModel] = useState(false)
  const [openAddWeekOffModel, setOpenAddWeekOffModel] = useState(false)
  const [getWeek, setGetWeek] = useState(false)

  useEffect(() => {
    if (!deleteEmployeeShiftPending) {
      if (deleteEmployeeShiftError) {
        // toast.error(deleteEmployeeShiftError?.errorMessage)
        return;
      }
      if (deleteEmployeeShiftRes) {
        if (deleteEmployeeShiftRes?.code === 200) {
          getAllShiftDetails(shiftData)
          dispatch(shiftManagementAction.handleClearShiftStore("DELECT_EMPLOYEE_SHIFT_STORE"))
        }
      }
    }
  }, [
    deleteEmployeeShiftRes,
    deleteEmployeeShiftError,
    deleteEmployeeShiftPending
  ]);


  const assignShiftModelOpen = () => {
    setOpenAssignShiftModel(!openAssignShiftModel)
  }

  const addWeekOffModelOpen = (data) => {
    setOpenAddWeekOffModel(!openAddWeekOffModel)
    if (data == 'data') {
      setGetWeek(!getWeek)
    }
  }
  useEffect(() => {
    getAllShiftDetails()
  }, [])
  useEffect(() => {
    if(shiftData){
    getAllShiftDetails(shiftData)
    }
  }, [shiftData])

  useEffect(()=>{
    if(isActiveShift){
    activeInactiveFunction(isActiveShift)
    }
  },[isActiveShift])

  const activeInactiveFunction = (isActiveShift)=>{
    getAllShiftDetails(isActiveShift)
  }

  const nextprev = async (dateRange, direction) => {
    const [start, end] = dateRange.split(' - ');
    const startDate = moment(start, 'DD MMM YYYY');
    const endDate = moment(end, 'DD MMM YYYY');

    if (direction === 'prev') {
      await getAllShiftDetails(shiftData, startDate.subtract(1, 'week'), endDate.subtract(1, 'week'))
    } else if (direction === 'next') {
      await getAllShiftDetails(shiftData, startDate.add(1, 'week'), endDate.add(1, 'week'))
    }
  }

  const getAllShiftDetails = async (shiftDatas, startDate, endDate) => {
    let data = {
      shift_id: shiftDatas?.id == "ejknrjk34324" ? '' : shiftDatas?.id,
      start_date: startDate ? startDate : moment().startOf('week').add(1, 'day').format('YYYY-MM-DD'),
      end_date: endDate ? endDate : moment().endOf('week').add(1, 'day').format('YYYY-MM-DD')
    }
    dispatch(shiftManagementAction.getAllShiftDetailsRequest(data))
    setLoadIng(true)
  }

  const openShiftModelfunction = (data) => {
    if (data == 'Data') {
      let params = {
        id: shiftData?.id
      }
      dispatch(shiftManagementAction.getShiftByIdRequest(params))
    }
    setOpenShiftModel(!openShiftModel)


  }

  return (
    <>
      <div className="d-flex align-items-center p-3">
        <h3 className="me-auto mb-0 fs-5 fw-semibold">{moment(headerDate[0]?.date).format('MMMM YYYY')}
          {/* {`${shiftCalanderMonth} ${shiftCalanderYear}`} */}
        </h3>
        <div className="customCalanderNav me-auto d-flex align-items-center">

          <img src="images/prev-icon.svg" style={{ cursor: "pointer" }} alt=""
            // onClick={()=>dispatch(getPreviousWeek())}
            onClick={e => nextprev(`${moment(headerDate[0].date).format('DD MMM YYYY')} - ${moment((headerDate[headerDate.length - 1].date)).format('DD MMM YYYY')}`, "prev")}
          />

          <span className="mx-3 d-block fs-7 fw-semibold mt-1">
            {moment(headerDate[0]?.date).format('DD MMM YYYY')} - {moment((headerDate[headerDate.length - 1]?.date)).format('DD MMM YYYY')}
            {/* {`${weekFirstDate.getDate()} ${months[weekFirstDate.getMonth()]} ${weekFirstDate.getFullYear()} - ${weekEndDate.getDate()} ${months[weekEndDate.getMonth()]} ${weekEndDate.getFullYear()}`} */}
          </span>

          <img src="images/next-icon.svg" style={{ cursor: "pointer" }} alt=""
            onClick={e => nextprev(`${moment(headerDate[0].date).format('DD MMM YYYY')} - ${moment((headerDate[headerDate.length - 1].date)).format('DD MMM YYYY')}`, "next")}
          />

        </div>
        {shiftData !== '' && shiftData?.shift_name !== 'All Shift' && shiftData?.is_active == true &&
          <div className="dropdown moredwn">
            <button
              className="btn btn-primary addShiftBtn me-2 "
              type="button"
              data-bs-toggle="dropdown"
              aria-expanded="false"
            >
              <i className="bi bi-three-dots-vertical" /> More
            </button>
            <ul className="dropdown-menu ">
              <li>
                <button
                  className="dropdown-item"
                  onClick={() => openShiftModelfunction('Data')}
                >
                  Shift Configuration
                </button>
              </li>
              <li>
                <button
                  className="dropdown-item border-bottom"
                  onClick={() => assignShiftModelOpen()}
                >
                  Assign Shift
                </button>
              </li>
              <li>
                <button
                  className="dropdown-item border-bottom"
                  onClick={() => addWeekOffModelOpen('data')}
                >
                  Add Week Off
                </button>
              </li>


            </ul>
          </div>}
      </div>
      <ShiftConfiguration
        openShiftModelfunction={openShiftModelfunction}
        openShiftModel={openShiftModel}
        shiftData={shiftData}
      />

      <AssignShift
        openAssignShiftModel={openAssignShiftModel}
        assignShiftModelOpen={assignShiftModelOpen}
        shiftData={shiftData}
        getAllShiftDetails={getAllShiftDetails}
        setOpenEditModel={setOpenEditModel}
      />

      <AddWeekOff
        openAddWeekOffModel={openAddWeekOffModel}
        addWeekOffModelOpen={addWeekOffModelOpen}
        shiftData={shiftData}
        getWeek={getWeek}
        getAllShiftDetails={getAllShiftDetails}
      />
    </>
  );
};

export default ShiftCalanderHeader;
