import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import { CustomButton } from '../../globalComponents';
import { shiftManagementAction } from '../../../core/resource/actions';
import { toast } from 'react-toastify';
import { addShiftSchema } from '../../../utils/ValidationSchema';
import { validateSchema } from '../../../utils/ValidationHelper';
import CustomError from '../../globalComponents/CustomError';
import moment from 'moment'
import CustomTimePicker from '../../globalComponents/CustomTimePicker';

const CreateAndUpdateEmailTemplate = (props) => {

    const { openModel, openCreateShiftModel, editShift, setEditShift, getAllShiftFunction } = props
    const { addShiftPending, addShiftRes, addShiftError } = useSelector(state => state.shift)
    const [errors, setErrors] = useState({});
    const dispatch = useDispatch();
    const [editorData, setEditorData] = useState('')
    const [shiftId, setShiftId] = useState('')
    const [isActive, setIsActive] = useState(false)
    const [totalworkingHours, setTotalWorkingHours] = useState()
    const [totalBreakHours, setTotalBreakHours] = useState()
    const [createShift, serCreateShift] = useState({
        shift_name: '',
        shift_code: '',
        color: '',
        is_active: false,
        shift_start_time: '',
        shift_end_time: '',
        break_start_time: '',
        break_end_time: '',
        early_arrival_tolerance: '',
        late_departure_tolerance: '',
        grace_start_time:'',
        grace_end_time:'',
        half_day_policy_time: '',
        full_day_policy_time: '',
        shift_type: ''
    });

    useEffect(() => {
        serCreateShift({
            shift_name: editShift?.shift_name || '',
            shift_code: editShift?.shift_code || '',
            color: editShift?.color || '',
            is_active: editShift?.is_active || '',
            shift_start_time: editShift?.shift_start_time || '',
            shift_end_time: editShift?.shift_end_time || '',
            break_start_time: editShift?.break_start_time || '',
            break_end_time: editShift?.break_end_time || '',
            early_arrival_tolerance: editShift?.early_arrival_tolerance || '',
            late_departure_tolerance: editShift?.late_departure_tolerance || '',
            grace_start_time:editShift?.grace_start_time || '',
            grace_end_time:editShift?.grace_end_time || '',
            half_day_policy_time: editShift?.half_day_policy_time || '',
            full_day_policy_time: editShift?.full_day_policy_time || '',
            shift_type: editShift?.shift_type || '',
        })
        setIsActive(editShift.is_active)
        setShiftId(editShift?.id)
    }, [editShift])

    useEffect(() => {
        if (!addShiftPending) {
            if (addShiftError) {
                toast.error(addShiftError?.errorMessage)
                return;
            }
            if (addShiftRes) {
                if (addShiftRes?.data?.code === 200) {
                    toast.success(addShiftRes?.data?.msg)
                    openCreateShiftModel()
                    dispatch(shiftManagementAction.handleClearShiftStore("HANDLE_CLEAR_ADD_SHIFT_STORE"))
                }
            }
        }
    }, [
        addShiftRes,
        addShiftError,
        addShiftPending
    ]);

    const handleChange = (event) => {
        const { name, value } = event.target;
        if (name === 'shift_name') {
            let short_code = value?.split(' ').map(word => word.charAt(0).toUpperCase()).join('');
            createShift.shift_code = short_code
        } else if (name === "shift_end_time") {
            const startTime = moment(createShift.shift_start_time, 'HH:mm:ss');
            const endTime = moment(value, 'HH:mm:ss');
            const isNightShift = startTime.isAfter(endTime);

            const endAdjusted = isNightShift ? endTime.add(1, 'day') : endTime;
            const duration = moment.duration(endAdjusted.diff(startTime));
            const formattedTime = `${String(duration.hours()).padStart(2, '0')}:${String(duration.minutes()).padStart(2, '0')}`;

            const shiftType = isNightShift ? "night" : "morning";
            setTotalWorkingHours(formattedTime);
            createShift.shift_type = shiftType
            createShift.full_day_policy_time = formattedTime
        } else if (name === 'break_end_time') {
            const startTime = moment(createShift.break_start_time, 'HH:mm:ss');
            const endTime = moment(value, 'HH:mm:ss');
            const isNightShift = startTime.isAfter(endTime);

            const endAdjusted = isNightShift ? endTime.add(1, 'day') : endTime;
            const duration = moment.duration(endAdjusted.diff(startTime));
            const formattedTime = `${String(duration.hours()).padStart(2, '0')}:${String(duration.minutes()).padStart(2, '0')}`;
            setTotalBreakHours(formattedTime)
        }

        serCreateShift({ ...createShift, [name]: value });
        setErrors(prev => ({ ...prev, [name]: undefined }));
    };

    const handleSubmitCreateShift = async () => {
        createShift.is_active = isActive
        const validationResponse = await validateSchema(addShiftSchema, createShift);
        if (validationResponse.isValid) {
            setErrors({});
            if (editShift) {
                createShift['_id'] = shiftId
                dispatch(shiftManagementAction.updateShiftRequest(createShift));
                openCreateShiftModel()
                getAllShiftFunction()
            } else {
                dispatch(shiftManagementAction.addShiftRequest(createShift));
                getAllShiftFunction()
            }

        } else {
            setErrors(validationResponse.errors);
            console.log(validationResponse.errors, "formattedErrors");
        }
    }

    useEffect(() => {
        if (openModel == false) {
            resetTempateData()
        }
    }, [openModel])


    const resetTempateData = () => {
        serCreateShift({
            shift_name: '',
            shift_code: '',
            color: '',
            is_active: '',
            shift_start_time: '',
            shift_end_time: '',
            break_start_time: '',
            break_end_time: '',
            early_arrival_tolerance: '',
            late_departure_tolerance: '',
            grace_start_time:'',
            grace_end_time:'',
            half_day_policy_time: '',
            full_day_policy_time: '',
            shift_type: '',
        })
        setEditShift('')
        setErrors({});
        setEditorData('')
    }


    return (
        <>
            <div className={`offcanvas offcanvas-end creatNewTicketOff ${openModel ? 'show' : ''}`}

                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                    width: "800px"
                }}
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">{editShift ? "Update" : "Create New"} Shift</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => openCreateShiftModel()}
                    />
                </div>
                <div className="offcanvas-body">
                    <div className="formPanel">
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor>Shift Name<span className='text-danger'>*</span></label>
                                </div>
                            </div>
                            {/* <div className="col-md-12">
                                <div className="form-group mb-3">
                                    <div className='row'>
                                        <div className="form-group col-md">
                                            <label htmlFor="exampleInputEmail1">Shift Name</label><span className='text-danger'>*</span>
                                            <input type="text" name="shift_name" value={createShift.shift_name} onChange={(e) => handleChange(e)} className="form-control" placeholder="Enter start time" />
                                            {errors?.shift_name && (
                                                <CustomError text={errors.shift_name} />
                                            )}
                                        </div>
                                        <div className="form-group col-md">
                                            <label htmlFor="exampleInputEmail1">Shift Code</label><span className='text-danger'>*</span>
                                            <input type="text" name="shift_code" value={(createShift.shift_code)?.toUpperCase()} onChange={(e) => handleChange(e)} className="form-control" placeholder="Enter end time" />
                                            {errors?.shift_code && (
                                        <CustomError text={errors.shift_code} />
                                    )}
                                        </div>
                                    </div>
                                </div>
                            </div> */}
                            <div className="col-md-9">
                                <div className="form-group mb-3">

                                    <input type="text" className="form-control fs-7"
                                        placeholder="Enter Shift Name"
                                        name='shift_name'
                                        value={createShift?.shift_name || ''}
                                        onChange={(e) => handleChange(e)}
                                    />

                                    {errors?.shift_name && (
                                        <CustomError text={errors.shift_name} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor>Shift Code<span className='text-danger'>*</span></label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">

                                    <input type="text" className="form-control fs-7"
                                        placeholder="Enter Shift Code"
                                        name='shift_code'
                                        value={(createShift.shift_code)?.toUpperCase() || ''}
                                        onChange={(e) => handleChange(e)}
                                    />

                                    {errors?.shift_code && (
                                        <CustomError text={errors.shift_code} />
                                    )}
                                </div>
                            </div>
                        </div>

                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor style={{ position: "relative", top: "40px" }}>Working Time<span className='text-danger'>*</span></label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <div className='row'>
                                        <div className="form-group col-md">
                                            <label htmlFor="exampleInputEmail1">Start time</label><span className='text-danger'>*</span>
                                            <input type="time" name="shift_start_time" value={createShift.shift_start_time} onChange={(e) => handleChange(e)} className="form-control" placeholder="Enter start time" />
                                            {errors?.shift_start_time && (
                                                <CustomError text={errors.shift_start_time} />
                                            )}
                                        </div>
                                        <div className="form-group col-md">
                                            <label htmlFor="exampleInputEmail1">End time</label><span className='text-danger'>*</span>
                                            <input type="time" name="shift_end_time" value={createShift.shift_end_time} onChange={(e) => handleChange(e)} className="form-control" placeholder="Enter end time" />
                                            {errors?.shift_end_time && (
                                                <CustomError text={errors.shift_end_time} />)}
                                        </div>
                                        <div className="form-group col-md">
                                            <label htmlFor="exampleInputEmail1">Shift Hours</label>
                                            <input type="text" name="" value={createShift.full_day_policy_time} readOnly className="form-control" placeholder="Shift Hours" />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor style={{ position: "relative", top: "40px" }}>Break Time<span className='text-danger'>*</span></label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <div className='row'>
                                        <div className="form-group col-md">
                                            <label htmlFor="exampleInputEmail1">Start time</label><span className='text-danger'>*</span>
                                            <input type="time" name="break_start_time" value={createShift.break_start_time} onChange={(e) => handleChange(e)} className="form-control" placeholder="Enter start time" />
                                            {errors?.break_start_time && (
                                                <CustomError text={errors.break_start_time} />
                                            )}
                                        </div>
                                        <div className="form-group col-md">
                                            <label htmlFor="exampleInputEmail1">End time</label><span className='text-danger'>*</span>
                                            <input type="time" name="break_end_time" value={createShift.break_end_time} onChange={(e) => handleChange(e)} className="form-control" placeholder="Enter end time" />
                                            {errors?.break_end_time && (
                                                <CustomError text={errors.break_end_time} />)}
                                        </div>
                                        <div className="form-group col-md">
                                            <label htmlFor="exampleInputEmail1">Break Hours</label>
                                            <input type="text" name="" value={totalBreakHours} readOnly className="form-control" placeholder="Break Hours" />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor style={{ position: "relative", top: "40px" }}>Punching Tolerance<span className='text-danger'>*</span></label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <div className='row'>
                                        <div className="form-group col-md">
                                            <label htmlFor="exampleInputEmail1">Early Coming Time</label><span className='text-danger'>*</span>
                                            <CustomTimePicker name="early_arrival_tolerance" value={createShift.early_arrival_tolerance} handleChange={handleChange} totalHours = {5} intervalMinutes={15} />
                                            {/* <input type="time" name="early_arrival_tolerance" value={createShift.early_arrival_tolerance} onChange={(e) => handleChange(e)} className="form-control" placeholder="Enter start time" /> */}
                                            {errors?.early_arrival_tolerance && (
                                                <CustomError text={errors.early_arrival_tolerance} />
                                            )}
                                        </div>
                                        <div className="form-group col-md">
                                            <label htmlFor="exampleInputEmail1">Let Going Time</label><span className='text-danger'>*</span>
                                            <CustomTimePicker name="late_departure_tolerance" value={createShift.late_departure_tolerance} handleChange={handleChange} totalHours = {5} intervalMinutes={15} />
                                            {/* <input type="time" name="late_departure_tolerance" value={createShift.late_departure_tolerance} onChange={(e) => handleChange(e)} className="form-control" placeholder="Enter end time" /> */}
                                            {errors?.late_departure_tolerance && (
                                                <CustomError text={errors.late_departure_tolerance} />)}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor style={{ position: "relative", top: "40px" }}>Grace Period<span className='text-danger'>*</span></label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <div className='row'>
                                        <div className="form-group col-md">
                                            <label htmlFor="exampleInputEmail1">Grace Start Time </label><span className='text-danger'>*</span>
                                            <CustomTimePicker name="grace_start_time" value={createShift.grace_start_time} handleChange={handleChange} totalHours = {5} intervalMinutes={15} />
                                            {errors?.grace_start_time && (
                                                <CustomError text={errors.grace_start_time} />
                                            )}
                                        </div>
                                        <div className="form-group col-md">
                                            <label htmlFor="exampleInputEmail1">Grace End Time</label><span className='text-danger'>*</span>
                                            <CustomTimePicker name="grace_end_time" value={createShift.grace_end_time} handleChange={handleChange} totalHours = {5} intervalMinutes={15} />
                                            {errors?.grace_end_time && (
                                                <CustomError text={errors.grace_end_time} />)}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor style={{ position: "relative", top: "40px" }}>Half/Full day policy<span className='text-danger'>*</span></label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <div className='row'>
                                        <div className="form-group col-md">
                                            <label htmlFor="exampleInputEmail1">Half day</label><span className='text-danger'>*</span>
                                            <input type="time" name="half_day_policy_time" value={createShift.half_day_policy_time} onChange={(e) => handleChange(e)} className="form-control" placeholder="Enter end time" />
                                            {errors?.half_day_policy_time && (
                                                <CustomError text={errors.half_day_policy_time} />)}
                                        </div>
                                        <div className="form-group col-md">
                                            <label htmlFor="exampleInputEmail1">Full day</label><span className='text-danger'>*</span>
                                            <input type="text" name="full_day_policy_time" value={createShift.full_day_policy_time} onChange={(e) => handleChange(e)} className="form-control" readOnly placeholder="Full day" />
                                            {errors?.full_day_policy_time && (
                                                <CustomError text={errors.full_day_policy_time} />
                                            )}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor>Shift Type<span className='text-danger'>*</span></label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">

                                    <select className="form-select" required
                                        name='shift_type'
                                        value={createShift?.shift_type}
                                        onChange={(e) => handleChange(e)}
                                    >
                                        <option value='' hidden selected>Select Required shift Type</option>
                                        <option value='morning'>Morning</option>
                                        <option value='night'>Night</option>
                                    </select>
                                    {errors?.shift_type && (
                                        <CustomError text={errors.shift_type} />
                                    )}
                                </div>
                            </div>
                        </div>

                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor>Shift Color<span className='text-danger'>*</span></label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">

                                    <input type="color" className='ColorData'
                                        placeholder="Enter Short Name"
                                        name='color'
                                        value={createShift?.color || ''}
                                        onChange={(e) => handleChange(e)}
                                    />
                                    {errors?.color && (
                                        <CustomError text={errors.color} />
                                    )}
                                </div>
                            </div>
                        </div>


                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor>is Active<span className='text-danger'>*</span></label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">

                                    <input type="checkbox" placeholder="Is Active"
                                        checked={isActive}
                                        onChange={(e) => setIsActive(!isActive)}
                                    />

                                    {errors?.is_active && (
                                        <CustomError text={errors.is_active} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="d-flex align-items-center justify-content-end gap-2">
                            <CustomButton
                                type="button"
                                classNames={"btn btn-primary gradientBg"}
                                handleClick={e => handleSubmitCreateShift()}
                                // isLoading={departmentData?updateDepartmentPending:addDepartmentPending}
                                text={`${editShift ? "Update" : "Create"} Shift`}
                            />
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default CreateAndUpdateEmailTemplate
