import React, { useState } from 'react';
import moment from 'moment';


const ShiftBulkUpload = (props) => {
    const { openUploadShiftModel, setOpenUploadShiftModel } = props
    const [startDate, setStartDate] =useState('')
    const [endDate, setEndDate] = useState('')
    const [bulkUploadFile,setBulkUploadFile]=useState('')
    const [bulkUploadFileName,setBulkUploadFileName]=useState('')
    const [showDownloadButton, setShowDownloadButton] = useState(true)
    const [fileUrl, setFileUrl] = useState('')

    const handleFileChange=(event)=>{
        setBulkUploadFile(event.target.files[0])
      }

    const handleStartDate = data => {
        setStartDate(data);
        setEndDate('');
      };
      const HandleSetEndDate=(data) =>{
            setEndDate(data)
            // simpleExlShiftReport(e)

      }
      const generateXLSXFile = () => {
        window.open(fileUrl, '_blank') 
    };
    return (
        <>
            <div className={`modal editShift fade ${openUploadShiftModel ? 'show' : ''}`}
                tabIndex="-1"
                style={{ display: openUploadShiftModel ? 'block' : 'none', opacity: openUploadShiftModel ? 1 : 0 }}>
                <div className="modal-dialog modal-dialog-centered">
                    <div className="modal-content rounded-0 border-0">
                        <div className="modal-header justify-content-between align-items-center">
                            <h3 className="fs-6 fw-semibold mb-0">Shift Bulk Upload</h3>
                            <button
                                type="button"
                                className="btnClose border-0 bg-transparent"
                                onClick={() => setOpenUploadShiftModel(!openUploadShiftModel)}
                            >
                                <img src="images/close-icon.svg" alt="" width="20" />
                            </button>
                        </div>

                        <div className="modal-body">
                            <form>
                                <div className='row'>
                                    <div className="form-group col-md-3">
                                        <label htmlFor="exampleInputEmail1" style={{ fontSize: "20px" }}>Start Date <span style={{ color: 'red' }}>*</span></label>
                                    </div>
                                    <div className="form-group col-md">
                                        <input type="date" name="shift_name"
                                            value={startDate}
                                            onChange={e => handleStartDate(e.target.value)}
                                            min={moment().startOf('month').format('YYYY-MM-DD')}
                                            className="form-control" placeholder="Enter Start Date" />
                                    </div>
                                </div>
                                <div className='row'>
                                    <div className="form-group col-md-3">
                                        <label htmlFor="exampleInputEmail1" style={{ fontSize: "20px" }}>End Date <span style={{ color: 'red' }}>*</span></label>
                                    </div>
                                    <div className="form-group col-md">
                                        <input type="date" name="shift_name"
                                            className="form-control" placeholder="Enter End Date"
                                            value={endDate}
                                            onChange={e => HandleSetEndDate(e.target.value)}
                                            min={moment(startDate).format('YYYY-MM-DD')}

                                        />
                                    </div>
                                </div>
                                {showDownloadButton &&
                                    <div className='row'>
                                        <div className="form-group col-md-12">
                                            <button
                                                className="btn btn-info float-right ms-5"
                                                title=""
                                                onClick={generateXLSXFile}
                                                style={{ backgroundColor: "cornflowerblue", color: "black" }}
                                            >
                                                Download Sample
                                            </button>
                                        </div>
                                    </div>}
                                <div className='row'>
                                    <div className="form-group col-md-3">
                                        <label htmlFor="exampleInputEmail1" style={{ fontSize: "20px" }}>Bulk Upload <span style={{ color: 'red' }}>*</span></label>
                                    </div>
                                    <div className="form-group col-md">
                                        <input
                                            type="file"
                                            placeholder=""
                                            className="is-untouched is-pristine av-invalid form-control"
                                            name="file"
                                            id="Name"
                                            onChange={(e) => handleFileChange(e)}
                                            required
                                        />
                                    </div>
                                </div>

                            </form>
                        </div>

                        <div className="modal-footer justify-content-center">
                            <button className="btn btn-org-gradient text-white px-5" >Save</button>
                        </div>
                    </div>
                </div>
            </div>

        </>
    )
}

export default ShiftBulkUpload