import React, { useEffect, useState } from "react";
import ShiftSchedual from "../components/admin/shiftManagement/ShiftSchedual";
import ShiftCalender from "../components/admin/shiftManagement/ShiftCalender";
import {useDispatch, useSelector } from "react-redux";
import {shiftManagementAction} from '../core/resource/actions'
import ApproveRejectModal from '../components/modal/ApproveRejectModal'
import ShiftEditModel from "../components/admin/shiftManagement/ShiftEditModel";
import ShiftBulkUpload from "../components/admin/shiftManagement/ShiftBulkUpload";
import { CustomSpinner } from "../components/globalComponents";


const ShiftManagement = () => {
  const dispatch = useDispatch()
  const {getAllShiftPending,getAllShiftRes,getAllShiftError,deleteShiftPending,deleteShiftRes,deleteShiftError,
    activeOrInactivePending,
  activeOrInactiveRes,
  activeOrInactiveError,
  } = useSelector(state =>state.shift)
  const [getAllShift, setAllShift] = useState([])
  const [deleteshift,setDeletShift] = useState('')
  const [isActiveDeletModel, setIsActiveDeletMode] = useState(false)
  const [isActiveOrIncatveModel, setIsActiveOrInActiveMode] = useState(false)
  const [isActivedata, setisActiveData] = useState()
  const [isActiveShift, setIsActive]= useState('')
  const [shiftData,setShiftData] = useState('')
  const [editMode, setEditMode] = useState(true)
  const [openEditModel, setOpenEditModel] = useState(false)
  const [assignTemporaryShift, setAssignTemporaryShift] = useState('')
  const [openUploadShiftModel, setOpenUploadShiftModel] = useState(false)
  const [loading,setLoadIng] = useState(false)


  const setDeletShifts = (data)=>{
    setIsActiveDeletMode(!isActiveDeletModel)
    setDeletShift(data)
  }

  const setActivebtn = (data)=>{
    setIsActiveOrInActiveMode(!isActiveOrIncatveModel)
    setisActiveData(data)
  }
  
  useEffect(()=>{
    getAllShiftFunction()
  },[])

  const getAllShiftFunction = ()=>{
    dispatch(shiftManagementAction.getAllShiftRequest())
  }

  useEffect(() => {
    if (!getAllShiftPending) {
        if (getAllShiftError) {
          setAllShift([])
            return;
        }
        if (getAllShiftRes) {
            if (getAllShiftRes?.code == 200) {
              let Temp = getAllShiftRes?.data?.data
              Temp?.unshift({ shift_name: "All Shift", id: 'ejknrjk34324', shift_start_time: "00:00", shift_end_time: "00:00" ,short_name: undefined})
              setAllShift(Temp)
            }
        }
    }
}, [
  getAllShiftPending,
  getAllShiftRes,
  getAllShiftError
]);

useEffect(() => {
  if (!deleteShiftPending) {
    if (deleteShiftError) {
      dispatch(shiftManagementAction.handleClearShiftStore('HANDLE_CLEAR_DELETE_SHIFT_STORE'))
      return;
    }
    if (deleteShiftRes) {
      if (deleteShiftRes?.code == 200) {
        dispatch(shiftManagementAction.handleClearShiftStore('HANDLE_CLEAR_DELETE_SHIFT_STORE'))
        getAllShiftFunction()
      }
    }
  }
}, [
  deleteShiftRes,
  deleteShiftError,
  deleteShiftPending
]);

useEffect(() => {
  if (!activeOrInactivePending) {
    if (activeOrInactiveError) {
      dispatch(shiftManagementAction.handleClearShiftStore('ACTIVE_INACTIVE_SHIFT_STORE'))
      return;
    }
    if (activeOrInactiveRes) {
      if (activeOrInactiveRes?.code == 200) {
        dispatch(shiftManagementAction.handleClearShiftStore('ACTIVE_INACTIVE_SHIFT_STORE'))
        setIsActive(shiftData)
        getAllShiftFunction()
      }
    }
  }
}, [
  activeOrInactiveRes,
  activeOrInactiveError,
  activeOrInactivePending
]);

const onDeleteCancelShift = (type)=>{
  if (type) {
    const params = {
      _id: deleteshift?.id
    }
    dispatch(shiftManagementAction?.deleteShiftRequest(params))
    setIsActiveDeletMode(!isActiveDeletModel)
  } else {
    setIsActiveDeletMode(!isActiveDeletModel)
  }
}

const onShiftactive = (type)=>{
  if(type){
    const params = {
      id:isActivedata?.id,
      is_active:isActivedata?.is_active == true ? false : true
    }
    dispatch(shiftManagementAction?.activeOrInactiveRequest(params))
    setIsActiveOrInActiveMode(!isActiveOrIncatveModel)
  } else {
    setIsActiveOrInActiveMode(!isActiveOrIncatveModel)
  }
}

  return (
    <>
      <ShiftSchedual 
      getAllShift={getAllShift}
      getAllShiftFunction={getAllShiftFunction}
      setDeletShift={setDeletShifts}
      setActivebtn={setActivebtn}
      setShiftData={setShiftData}
      editMode={editMode}
      setEditMode={setEditMode}
      openUploadShiftModel={openUploadShiftModel}
      setOpenUploadShiftModel={setOpenUploadShiftModel}/>
      
      <ShiftCalender 
      setLoadIng={setLoadIng}
      shiftData={shiftData}
      isActiveShift={isActiveShift}
      editMode={editMode}
      openEditModel={openEditModel}
      setOpenEditModel={setOpenEditModel}
      setAssignTemporaryShift={setAssignTemporaryShift}
      />

      {isActiveDeletModel && (
        <ApproveRejectModal
          onApproveReject={onDeleteCancelShift}
          isShow={isActiveDeletModel}
          desc={"You want to delete this Shift"}
          // btnRightLoading={deleteEmailTemplatePending}
        />)
      }
      {isActiveOrIncatveModel && (
        <ApproveRejectModal
          onApproveReject={onShiftactive}
          isShow={isActiveOrIncatveModel}
          desc={`You want to this shift ${isActivedata.is_active == true ? 'Inactve' : 'active'} `}
          isLoading={activeOrInactivePending}
        />)
      }
      {openEditModel && 
      <ShiftEditModel 
      openEditModel={openEditModel}
      setOpenEditModel={setOpenEditModel}
      assignTemporaryShift={assignTemporaryShift}
      getAllShift={getAllShift}
      />
      }
      {openUploadShiftModel && 
      <ShiftBulkUpload 
      openUploadShiftModel={openUploadShiftModel}
      setOpenUploadShiftModel={setOpenUploadShiftModel}
      />}

    {loading && <CustomSpinner isShow={loading} size="lg" />}
    </>
  );
};

export default ShiftManagement;
