import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import { CustomButton } from '../../globalComponents';
import { shiftManagementAction } from '../../../core/resource/actions';
import { toast } from 'react-toastify';
import { addShiftSchema } from '../../../utils/ValidationSchema';
import { validateSchema } from '../../../utils/ValidationHelper';
import CustomError from '../../globalComponents/CustomError';
import { ShiftcolorOption } from '../../../utils/Constants.config';
import CustomTimePicker from '../../globalComponents/CustomTimePicker';

const CreateAndUpdateEmailTemplate = (props) => {

    const { openShiftModel, openShiftModelfunction, editShift, shiftData } = props
    const { addShiftConfigurationPending, addShiftConfigurationRes, addShiftConfigurationError, getShiftByIdPending,getShiftByIdRes,
        getShiftByIdError,
        getAllShiftCalendarError,getAllShiftCalendarRes,getAllShiftCalendarPending} = useSelector(state => state.shift)
    const [errors, setErrors] = useState({});
    const [overTime, setOverTime] = useState(false)
    const [isActive, setIsActive] = useState(false)
    const dispatch = useDispatch();
    const [editorData, setEditorData] = useState('')
    const [workingDay, setWorkingDay] = useState('')
    const [weekOff, setweekOff] = useState()
    const [AllCalendar, setAllCalendar] = useState([])
    const setWorkingDayfun = (data) => {
        setWorkingDay(data)
    }
    const [shiftConfigurationdata, setShiftConfigurationdata] = useState({
        shift_id: shiftData?.id,
        over_time: '',
        minimum_ot_hours: '',
        maximum_ot_hours: '',
        calendar_id:workingDay,
        is_default:isActive
    })
    useEffect(() => {
        if (!addShiftConfigurationPending) {
            if (addShiftConfigurationError) {
                toast.error(addShiftConfigurationError?.errorMessage)
                return;
            }
            if (addShiftConfigurationRes) {
                if (addShiftConfigurationRes?.data?.code === 200) {
                    toast.success(addShiftConfigurationRes?.data?.msg)
                    openShiftModelfunction()
                    dispatch(shiftManagementAction.handleClearShiftStore("HANDLE_CLEAR_ADD_SHIFT_CONFIGURATION_STORE"))
                }
            }
        }
    }, [
        addShiftConfigurationRes,
        addShiftConfigurationError,
        addShiftConfigurationPending
    ]);

    useEffect(() => {
        if (!getShiftByIdPending) {
            if (getShiftByIdError) {
                toast.error(getShiftByIdError?.errorMessage)
                return;
            }
            if (getShiftByIdRes) {
                if (getShiftByIdRes?.data?.code === 200) {
                    let apidata = getShiftByIdRes?.data?.data?.data
                    setOverTime(apidata?.is_ot_time)
                    setShiftConfigurationdata({
                        minimum_ot_hours: apidata?.minimum_ot_hours,
                        maximum_ot_hours: apidata?.maximum_ot_hours,
                    })
                    setWorkingDay(apidata.calendar_id)
                    setIsActive(apidata?.is_default)
                }
            }
        }
    }, [
        getShiftByIdRes,
        getShiftByIdError,
        getShiftByIdPending
    ]);

    const handleChange = (event) => {
        const { name, value } = event.target;
        setShiftConfigurationdata({ ...shiftConfigurationdata, [name]: value });
        setErrors((pre) => ({
            ...pre,
            [name]: undefined
        }))
    }

    const handleSubmitCreateShiftConfiguration = async () => {
        shiftConfigurationdata.shift_id = shiftData?.id
        shiftConfigurationdata.is_OT_time = overTime
        shiftConfigurationdata.calendar_id = workingDay
        // const validationResponse = await validateSchema(addShiftSchema, shiftConfigurationdata);
        // if (validationResponse.isValid) {
        setErrors({});
        dispatch(shiftManagementAction.addShiftConfigurationRequest(shiftConfigurationdata));
        // } else {
        // setErrors(validationResponse.errors);
        // console.log(validationResponse.errors, "formattedErrors");
        // }
    }

    useEffect(() => {
        if (openShiftModel == false) {
            resetTempateData()
        }
    }, [openShiftModel])

    useEffect(() => {
        getAllShiftCalendar()
      }, [])
    
      const getAllShiftCalendar = () => {
        dispatch(shiftManagementAction.getAllShiftCalendarRequest())
      }

      useEffect(() => {
        if (!getAllShiftCalendarPending) {
            if (getAllShiftCalendarError) {
                setAllCalendar([])
                return;
            }
            if (getAllShiftCalendarRes) {
                if (getAllShiftCalendarRes?.data?.code == 200) { 
                    setAllCalendar(getAllShiftCalendarRes?.data?.data?.data?.data)
                }
            }
        }
    }, [
        getAllShiftCalendarPending,
        getAllShiftCalendarRes,
        getAllShiftCalendarError
    ]);

    const resetTempateData = () => {
        setShiftConfigurationdata({
            shift_id: '',
            over_time: '',
            minimum_ot_hours: '',
            maximum_ot_hours: '',
            working_days: '',
            alternative_saturday_off: '',
            week_off: ''
        })
        setErrors({});
        setOverTime(false)
        setIsActive(false)
        setEditorData('')
        setweekOff([])
        setWorkingDay([])
    }


    return (
        <>
            <div className={`offcanvas offcanvas-end applyLeaveCanvas ${openShiftModel ? 'show' : ''}`}

                tabIndex={-1}
                style={{
                    visibility: openShiftModel ? 'visible' : 'hidden',
                    width: "800px"
                }}>
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">Shift Management</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => openShiftModelfunction()}
                    />
                </div>
                <div className="offcanvas-body">
                    <div className="table-responsive tableDiv shiftmanagementtbl">
                        <table className="table">
                            <tbody>

                                <tr>
                                    <td width="30%">
                                        <div className="d-flex align-items-center"><span className="srNum">1</span><b
                                            className="ps-3 text-primary"> Shift start time</b></div>
                                    </td>
                                    <td>{shiftData?.shift_start_time}</td>
                                </tr>
                                <tr>
                                    <td>
                                        <div className="d-flex align-items-center"><span className="srNum">2</span><b
                                            className="ps-3 text-primary"> Shift start time</b></div>
                                    </td>
                                    <td>{shiftData?.shift_end_time}</td>
                                </tr>
                                <tr>
                                    <td>
                                        <div className="d-flex align-items-center"><span className="srNum">3</span><b
                                            className="ps-3 text-primary"> Over Time (OT)</b></div>
                                    </td>
                                    <td>

                                        <input type="checkbox" placeholder="Is Active"
                                            checked={overTime}
                                            onChange={(e) => setOverTime(!overTime)}
                                        />
                                    </td>
                                </tr>
                                {overTime == true &&
                                    <tr>
                                        <td>
                                            <div className="d-flex align-items-center"><b
                                                className="ps-3 text-primary"> </b></div>
                                        </td>
                                        <td>
                                            <div className='row' >
                                                <div className="form-group col-md">
                                                    <label htmlFor="exampleInputEmail1">Minimum OT hours par days</label>
                                                    {/* <input type="time" name="minimum_ot_hours" value={shiftConfigurationdata.minimum_ot_hours} onChange={(e) => handleChange(e)} className="form-control" placeholder="Enter start time" /> */}
                                                    <CustomTimePicker name="minimum_ot_hours" value={shiftConfigurationdata.minimum_ot_hours} handleChange={handleChange} totalHours = {5} intervalMinutes={15} />
                                                    {errors?.minimum_ot_hours && (
                                                        <CustomError text={errors.minimum_ot_hours} />
                                                    )}
                                                </div>
                                                <div className="form-group col-md">
                                                    <label htmlFor="exampleInputEmail1">Maximum OT hours par days</label>
                                                    {/* <input type="time" name="maximum_ot_hours" value={shiftConfigurationdata.maximum_ot_hours} onChange={(e) => handleChange(e)} className="form-control" placeholder="Enter end time" /> */}
                                                    <CustomTimePicker name="maximum_ot_hours" value={shiftConfigurationdata.maximum_ot_hours} handleChange={handleChange} totalHours = {5} intervalMinutes={15} />
                                                    {errors?.maximum_ot_hours && (
                                                        <CustomError text={errors.maximum_ot_hours} />)}
                                                </div>
                                            </div>
                                        </td>
                                    </tr>}

                                <tr>
                                    <td>
                                        <div className="d-flex align-items-center"><span className="srNum">4</span><b
                                            className="ps-3 text-primary"> Is Default</b></div>
                                    </td>
                                    <td>
                                        <input type="checkbox" placeholder="Is Active"
                                            checked={isActive}
                                            onChange={(e) => setIsActive(!isActive)}
                                        />
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <div className="d-flex align-items-center"><span className="srNum">5</span><b
                                            className="ps-3 text-primary">add calendar</b></div>
                                    </td>
                                    <td>
                                        <div className="d-flex workingradio">
                                            <div className="shiftInpt">
                                                <select className="form-select" required
                                                    name='shift_type'
                                                    value={workingDay}
                                                    onChange={(e) => setWorkingDayfun(e.target.value)}
                                                >
                                                    <option value='' hidden selected>Select Required Working Days</option>
                                                    {AllCalendar && AllCalendar?.map(item => (
                                                        <option value={item?.id}>{item?.title}</option>
                                                    ))}
                                                   
                                                    {/* <option value='6'>6 Days</option>
                                                    <option value='7'>7 Days</option>
                                                    <option value='alternative_saturday'>Alternative Saturday Off</option> */}
                                                </select>
                                            </div>
                                        </div>
                                    </td>

                                </tr>
                                {/* {workingDay == 'alternative_saturday' &&
                                    <tr>
                                        <td>
                                            <div className="d-flex align-items-center"><span className="srNum">6</span><b
                                                className="ps-3 text-primary">Saturdays</b></div>
                                        </td>
                                        <td>
                                            <div className="d-flex workingradio">
                                                <div className="d-flex gap-2 checkShift">
                                                    <div className="form-check">
                                                        <input className="form-check-input" type="checkbox" value="1" name='1' onClick={alternativeSaturdayFun} id="first" />
                                                        <label className="form-check-label" htmlFor="first">
                                                            1st
                                                        </label>
                                                    </div>
                                                    <div className="form-check">
                                                        <input className="form-check-input" type="checkbox" value="2" name='2' onClick={alternativeSaturdayFun} id="second" />
                                                        <label className="form-check-label" htmlFor="second">
                                                            2st
                                                        </label>
                                                    </div>
                                                    <div className="form-check">
                                                        <input className="form-check-input" type="checkbox" value="3" name='3' onClick={alternativeSaturdayFun} id="third" />
                                                        <label className="form-check-label" htmlFor="third">
                                                            3st
                                                        </label>
                                                    </div>
                                                    <div className="form-check">
                                                        <input className="form-check-input" type="checkbox" value="4" name='4' onClick={alternativeSaturdayFun} id="fourth " />
                                                        <label className="form-check-label" htmlFor="fourth ">
                                                            4st
                                                        </label>
                                                    </div>
                                                    <div className="form-check">
                                                        <input className="form-check-input" type="checkbox" value="5" name='5' onClick={alternativeSaturdayFun} id="five " />
                                                        <label className="form-check-label" htmlFor="five ">
                                                            5st
                                                        </label>
                                                    </div>
                                                </div>
                                            </div>
                                        </td>

                                    </tr>}
                                {workingDay != '7' && workingDay != 'alternative_saturday' &&
                                    <tr>
                                        <td>
                                            <div className="d-flex align-items-center"><span className="srNum">7</span><b
                                                className="ps-3 text-primary"> Week Off</b></div>
                                        </td>
                                        <td>
                                            <div className="d-flex gap-2 checkShift">
                                                <div className="form-check">
                                                    <input className="form-check-input" type="checkbox" value="1" name='1' id="first" onClick={weekOffFun} checked={weekOff?.includes("1")} />
                                                    <label className="form-check-label" htmlFor="first">
                                                        Monday
                                                    </label>
                                                </div>
                                                <div className="form-check">
                                                    <input className="form-check-input" type="checkbox" value="2" name='2' id="second" onClick={weekOffFun} checked={weekOff?.includes("2")} />
                                                    <label className="form-check-label" htmlFor="second">
                                                        Tuesday
                                                    </label>
                                                </div>
                                                <div className="form-check">
                                                    <input className="form-check-input" type="checkbox" value="3" name='3' id="third" onClick={weekOffFun} checked={weekOff?.includes("3")} />
                                                    <label className="form-check-label" htmlFor="third">
                                                        Wednesday
                                                    </label>
                                                </div>
                                                <div className="form-check">
                                                    <input className="form-check-input" type="checkbox" value="4" name='4' id="fourth " onClick={weekOffFun} checked={weekOff?.includes("4")} />
                                                    <label className="form-check-label" htmlFor="fourth ">
                                                        Thursday
                                                    </label>
                                                </div>
                                                <div className="form-check">
                                                    <input className="form-check-input" type="checkbox" value="5" name='5' id="five " onClick={weekOffFun} checked={weekOff?.includes("5")} />
                                                    <label className="form-check-label" htmlFor="five ">
                                                        Friday
                                                    </label>
                                                </div>
                                                <div className="form-check">
                                                    <input className="form-check-input" type="checkbox" value="6" name='6' id="five " onClick={weekOffFun} checked={weekOff?.includes("6")} />
                                                    <label className="form-check-label" htmlFor="five ">
                                                        Saturday
                                                    </label>
                                                </div>
                                                <div className="form-check">
                                                    <input className="form-check-input" type="checkbox" value="7" name='7' id="five " onClick={weekOffFun} checked={weekOff?.includes("7")} />
                                                    <label className="form-check-label" htmlFor="five ">
                                                        Sunday
                                                    </label>
                                                </div>
                                            </div>
                                        </td>

                                    </tr>} */}
                            </tbody>
                        </table>

                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        {/* <button type="button" className="border-0 p-0 bg-transparent cancelBtn">Back</button> */}
                        <button type="button" className="btn btn-primary gradientBg" onClick={() => handleSubmitCreateShiftConfiguration()}>Submit</button>
                    </div>
                </div>
            </div>
        </>
    )
}

export default CreateAndUpdateEmailTemplate
