import React,{useState} from 'react'
import ShiftMenuDropdown from './ShiftMenuDropdown'
import {useDispatch} from 'react-redux'


const ShiftInfoCard = ({card ,index,setEditShift,openCreateShiftModel,setDeletShift,setShiftData,setActivebtn,editMode}) => {
  const dispatch=useDispatch()
  const [activebtn, setActiveBtn] = useState([0])

  const clickShiftData = (data, index)=>{
    setShiftData(data)
    setActiveBtn([index])
  }
  return (
      <li key={card?.id} className="nav-item" style={{backgroundColor:"#FFFFFF30",marginRight:"10px"}}>
      <a href="#shiftTab1" data-bs-toggle="tab" className={`nav-link w-100 ${activebtn.includes(index) ? 'active' : ''}`} onClick={e => clickShiftData(card, index)}>
        <span>{card?.shift_name} {card?.shift_code === null || card?.shift_code === undefined ? "":`(${card?.shift_code})`}</span>
        <strong>{`${card?.shift_start_time } - ${card?.shift_end_time}`} </strong>
      </a>
      {card?.shift_code !== undefined && editMode &&
      <ShiftMenuDropdown 
      card={card}
      setEditShift={setEditShift}
      openCreateShiftModel={openCreateShiftModel}
      setDeletShift={setDeletShift}
      setActivebtn={setActivebtn}
      />}

   
    </li>
  )
}

export default ShiftInfoCard