import React from "react";
import { convertToTS } from "../../../utils/AppHelper";

const EmployeeShiftNameTime = ({ shiftTime,editMode ,setOpenEditModel,openEditModel}) => {
  const shiftName = shiftTime &&
    shiftTime?.shift_name[0] == "WO" || shiftTime?.shift_name[0] == "PH"
      ? shiftTime?.shift_name[0]
      : shiftTime?.shift_name[0].split(" ")[0];
      const theam=shiftName==="PH"?"purpleColor":shiftName=="WO"?"skyColor":null
  return (
    <td>
      <div className="shiftBox purpleColor" style={{ background: `${shiftTime.color}` }}>
        <div className="d-flex align-items-center gap-1">
          {!editMode &&
          <a   className="editShiftTg" onClick={()=>setOpenEditModel(!openEditModel)}>
            <img src="images/editIcon1.svg" alt="" /></a>}
          <p style={{cursor: "pointer"}}>
            {/* <b>{shiftTime?.shift_name[0].split(" ")[0]}</b>
            {shiftTime?.shift_name[0].split(" ")[1]} */}

            <b>{shiftTime?.shift_name[0].replace(/\(|\)/g, ',').split(',')[0]}</b>
            {shiftTime?.shift_name[0].replace(/\(|\)/g, ',').split(',')[1]}
          </p>
        </div>
      </div>
    </td>
  );
};

export default EmployeeShiftNameTime;
