import React, { useState } from 'react';
import { convertShiftTime, convertToTS } from '../../../utils/AppHelper'
import { useDispatch, useSelector } from "react-redux";
import { shiftManagementAction } from '../../../core/resource/actions';

const ShiftEditModel = (props) => {
    const { openEditModel, setOpenEditModel, assignTemporaryShift, getAllShift } = props;
    const dispatch = useDispatch()
    const [shiftStatus, setShiftStatus] = useState('')
    const [startDate, setStartDate] = useState();
    const [endDate, setEndDate] = useState();
    const [shiftId,setShiftId] = useState('')
    const [status, setStatus] = useState(false)
    const handleClose = () => {
        setOpenEditModel(false);
    };
    const handleShift = (status, data) => {
        setStatus(status)
        setShiftStatus(data)

    }
    const handleSubmitAssignShift = () => {
        let data = {
            assignment_type:'temporary',
            shift_id:shiftId,
            employee_id:[assignTemporaryShift?._id],
            start_date:startDate,
            end_date:endDate
        }
        dispatch(shiftManagementAction.assignShiftRequest(data));
    }

    return (
        <>
            <div className={`modal editShift fade ${openEditModel ? 'show' : ''}`}
                tabIndex="-1"
                style={{ display: openEditModel ? 'block' : 'none', opacity: openEditModel ? 1 : 0 }}>
                <div className="modal-dialog modal-dialog-centered">
                    <div className="modal-content rounded-0 border-0">
                        <div className="modal-header justify-content-between align-items-center">
                            <h3 className="fs-6 fw-semibold mb-0">Edit Shift</h3>
                            <button
                                type="button"
                                className="btnClose border-0 bg-transparent"
                                onClick={handleClose}
                            >
                                <img src="images/close-icon.svg" alt="" width="20" />
                            </button>
                        </div>

                        <div className="modal-body">
                            <div className="currentShift">
                                <p className="py-2"><b>Current Shift:</b> {convertShiftTime(assignTemporaryShift?.shift_start_time)} - {convertShiftTime(assignTemporaryShift?.shift_end_time)}</p>
                                <div className="editShiftGroup" id="editShiftDiv">
                                    <div className="mb-2">
                                        <select className="form-select" onChange={(e)=>setShiftId(e.target.value)}>
                                            {getAllShift.map(item=>(
                                                <option value={item._id}>{item?.shift_name} ({convertShiftTime(item?.shift_start_time)} - {convertShiftTime(item?.shift_end_time)})</option>
                                            ))}
                                        </select>
                                    </div>
                                    {status &&
                                        <div className="d-flex gap-2">
                                            <div className="form-group col-md">
                                                <label htmlFor="exampleInputEmail1">Start Date</label><span style={{ color: "red" }}>*</span>
                                                <input type="date" name="shiftStartDate" onChange={(e) => setStartDate(e.target.value)} className="form-control" placeholder="Enter start time" />
                                            </div>
                                            <div className="form-group col-md">
                                                <label htmlFor="exampleInputEmail1">End Date</label><span style={{ color: "red" }}>*</span>
                                                <input type="date" name="shiftEndDate" onChange={(e) => setEndDate(e.target.value)} min={convertToTS(startDate, 'YYYY-MM-DD')} className="form-control" placeholder="Enter end time" />

                                            </div>

                                        </div>}
                                </div>
                            </div>
                            {!status &&
                                <div className="d-flex shiftBtnGroup gap-3">
                                    <button type="button" className="btn btn-outline-primary editShiftButton" onClick={() => handleShift(true, 'temp')}>
                                        <img src="images/editShift.svg" alt="" width="16" /> Edit Shift
                                    </button>
                                    <button type="button" className="btn btn-outline-primary addShiftButton" onClick={() => handleShift(true, 'both')}>
                                        <img src="images/add-icon.svg" alt="" width="16" /> Add another Shift
                                    </button>
                                </div>}
                        </div>

                        <div className="modal-footer justify-content-center">
                            <button className="btn btn-org-gradient text-white px-5" onClick={()=>handleSubmitAssignShift()}>Save</button>
                        </div>
                    </div>
                </div>
            </div>

            {/* Backdrop */}
            {openEditModel && <div className="modal-backdrop fade show"></div>}
        </>
    );
};

export default ShiftEditModel;
