import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import { CustomButton } from '../../globalComponents';
import { shiftManagementAction } from '../../../core/resource/actions';
import { toast } from 'react-toastify';
import { addShiftSchema } from '../../../utils/ValidationSchema';
import { validateSchema } from '../../../utils/ValidationHelper';
import CustomError from '../../globalComponents/CustomError';

const AddWeekOff = (props) => {

    const { openAddWeekOffModel, addWeekOffModelOpen, editShift, shiftData, getWeek,getAllShiftDetails } = props
    const dispatch = useDispatch()
    const {
        getEmployeebyShiftidPending,
        getEmployeebyShiftidRes,
        getEmployeebyShiftidError,

        addWeekOffPending,
        addWeekOffRes,
        addWeekOffError,
    } = useSelector(state => state.shift)

    const [weekdays, setweekday] = useState([1, 2, 3, 4, 5, 6, 7])
    const [weekoffs, setweekoffs] = useState([])
    const [newRoster, setNewRoster] = useState({})
    const [employeeData, setEmployeedata] = useState([])
    const [searchWeekoff, setSearchWeekoff] = useState([])

    useEffect(() => {
        if (!addWeekOffPending) {
            if (addWeekOffError) {
                toast.error(addWeekOffError?.errorMessage)
                return;
            }
            if (addWeekOffRes) {
                if (addWeekOffRes?.data?.code === 200) {
                    toast.success(addWeekOffRes?.data?.msg)
                    addWeekOffModelOpen()
                    getAllShiftDetails(shiftData)
                    dispatch(shiftManagementAction.handleClearShiftStore("HANDLE_ADD_WEEK_OFF_STORE"))
                }
            }
        }
    }, [
        addWeekOffRes,
        addWeekOffError,
        addWeekOffPending
    ]);

    const searchWeekOff = (e) => {
        const filteredData = searchWeekoff.filter(item =>
          item.name.toLowerCase().includes(e?.toLowerCase()) ||
          item.emp_id?.toLowerCase().includes(e?.toLowerCase())
        );
        setEmployeedata(filteredData)
      };

    useEffect(() => {
        if (getWeek == true) {
            getEmployeeByShiftId()
        }
    }, [getWeek])

    const getEmployeeByShiftId = async () => {
        let data = {
            shift_id: shiftData.id
        }
        dispatch(shiftManagementAction.getEmployeeByShiftIdRequest(data))
    }

    useEffect(() => {
        if (!getEmployeebyShiftidPending) {
            if (getEmployeebyShiftidError) {
                return;
            }
            if (getEmployeebyShiftidRes) {
                if (getEmployeebyShiftidRes?.data?.code === 200) {
                    const EmployeeResData = getEmployeebyShiftidRes?.data?.data?.data
                    setEmployeedata(EmployeeResData)
                    const initialCheckedCheckboxes = {};
                    const initialWeekOffs = [];
                    EmployeeResData?.forEach(employee => {
                        initialCheckedCheckboxes[employee.employee_id] = {};
                        employee?.weekoff?.forEach(day => {
                            if (day === null) {
                                initialCheckedCheckboxes[employee.employee_id][0] = false;
                            } else {
                                initialCheckedCheckboxes[employee.employee_id][day] = true;
                            }
                        });
                        initialWeekOffs.push({ employee_id: employee.employee_id, week_off: employee.weekoff });
                    });
                    setNewRoster(initialCheckedCheckboxes);
                    setweekoffs(initialWeekOffs);
                    setSearchWeekoff(EmployeeResData)

                }
            }
        }
    }, [
        getEmployeebyShiftidRes,
        getEmployeebyShiftidError,
        getEmployeebyShiftidPending
    ]);

    const AddWeekoff = (employeeId, day) => {
        setweekoffs(prevState => {
            const index = prevState?.findIndex(item => item.employee_id === employeeId);
            if (index !== -1 && prevState[index].week_off.includes(day)) {
                const updatedWeekOff = prevState[index].week_off.filter(d => d !== day);
                if (updatedWeekOff.length === 0) {
                    return prevState.filter(item => item.employee_id !== employeeId);
                }
                return [
                    ...prevState.slice(0, index),
                    { ...prevState[index], week_off: updatedWeekOff },
                    ...prevState.slice(index + 1)
                ];
            }
            if (index === -1) {
                return [
                    ...prevState,
                    { employee_id: employeeId, week_off: [day] }
                ];
            }
            return [
                ...prevState.slice(0, index),
                { ...prevState[index], week_off: [...prevState[index].week_off, day] },
                ...prevState.slice(index + 1)
            ];
        });

        setNewRoster(prevState => ({
            ...prevState,
            [employeeId]: {
                ...prevState[employeeId],
                [day]: !prevState[employeeId]?.[day]
            }
        }));
    };

    const handleSubmitAddWeekoff = () => {
        let data = {
            weekoff_details: weekoffs
        }
        // const validationResponse = await validateSchema(addShiftSchema, shiftConfigurationdata);
        // if (validationResponse.isValid) {
        // setErrors({});
        dispatch(shiftManagementAction.addWeekOffRequest(data));

    }
    return (
        <>
            <div className={`offcanvas offcanvas-end applyLeaveCanvas ${openAddWeekOffModel ? 'show' : ''}`}

                tabIndex={-1}
                style={{
                    visibility: openAddWeekOffModel ? 'visible' : 'hidden',
                    width: "900px"
                }}>
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title fs-5 fw-bold text-primary">Add Week Off</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => addWeekOffModelOpen()}
                    />
                </div>
                <div class="offcanvas-body">
                    <div class="rounded-0 border-0">
                        <div class="justify-content-between align-items-center border-0">
                            <div className='headerWeek'>
                                <p class="mb-0"><b>Shift</b>: {shiftData.shift_name} ({shiftData.shift_start_time} - {shiftData.shift_end_time}) </p>
                            </div>
                        </div>
                        <div class="py-0 shiftChecks shiftChecksweek">
                            <div class="">
                                <div class="searchTeamBox mb-2 p-2">
                                    <div class="searchByName ">
                                        <input type="text" class="form-control" placeholder="Search" onChange={(e) => searchWeekOff(e.target.value)}/>
                                        <button type="button" class="nameSearchBtn"><img src="images/search2.svg" alt=""
                                            width="17" /></button>
                                    </div>
                                </div>
                                <div class="table-responsive weekoffTable addWeek p-3 pt-0">
                                    <table class="table">
                                        <tr>
                                            <th>
                                                All Users
                                            </th>
                                            <th class="text-center">Mon</th>
                                            <th class="text-center">Tue</th>
                                            <th class="text-center">Wed</th>
                                            <th class="text-center">Thu</th>
                                            <th class="text-center">Fri</th>
                                            <th class="text-center">Sat</th>
                                            <th class="text-center">Sun</th>
                                        </tr>
                                        {employeeData && employeeData.map(item => (
                                            <tr>
                                                <td>
                                                    <div class="d-flex align-items-center userInfoTd">
                                                        <figure class="m-0">
                                                            <img src="images/amber.png" alt="" />
                                                        </figure>
                                                        <h6>{item.name} {item.last_name}</h6>
                                                    </div>
                                                </td>
                                                {weekdays.map(day => (
                                                    <td key={day}>
                                                        <div className="cstmCheckbox">
                                                            <input
                                                                type="checkbox"
                                                                id={`check-${item.employee_id}-${day}`}
                                                                name={`${item.employee_id}-${day}`}
                                                                onClick={() => AddWeekoff(item.employee_id, day)}
                                                                value={day}
                                                                checked={newRoster[item.employee_id]?.[day]}
                                                            // checked={(item?.week_off).includes(weekoffs[item.emp_user_id]?.[day])}
                                                            />
                                                            <label htmlFor={`check-${item.employee_id}-${day}`} />
                                                        </div>
                                                    </td>
                                                ))}
                                                {/* <td>
                                                <div class="cstmCheckbox">
                                                    <input type="checkbox" id="check1" />
                                                    <label for="check1"></label>
                                                </div>
                                            </td>
                                            <td>
                                                <div class="cstmCheckbox">
                                                    <input type="checkbox" id="check2" />
                                                    <label for="check2"></label>
                                                </div>
                                            </td>
                                            <td>
                                                <div class="cstmCheckbox">
                                                    <input type="checkbox" id="check3" />
                                                    <label for="check3"></label>
                                                </div>
                                            </td>
                                            <td>
                                                <div class="cstmCheckbox">
                                                    <input type="checkbox" id="check4" />
                                                    <label for="check4"></label>
                                                </div>
                                            </td>
                                            <td>
                                                <div class="cstmCheckbox">
                                                    <input type="checkbox" id="check5" />
                                                    <label for="check5"></label>
                                                </div>
                                            </td>
                                            <td>
                                                <div class="cstmCheckbox">
                                                    <input type="checkbox" id="check6" />
                                                    <label for="check6"></label>
                                                </div>
                                            </td>
                                            <td>
                                                <div class="cstmCheckbox">
                                                    <input type="checkbox" id="check7" />
                                                    <label for="check7"></label>
                                                </div>
                                            </td> */}
                                            </tr>
                                        ))}

                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <button type="button" className="btn btn-primary gradientBg" onClick={handleSubmitAddWeekoff}>Submit</button>
                    </div>
                </div>
            </div>
        </>
    )
}

export default AddWeekOff;
